import { TextField, Drawer, Button, Box, List, Card, CardContent, Typography, Modal, Alert, ButtonGroup } from '@mui/material';
import { useAuth } from '../contexts/authentication';
import { useNavigate } from 'react-router-dom';
import { useServerData } from '../contexts/serverData';
import React, { useState } from 'react';

const serverDataTypes = require('../contexts/serverData');
const axios = require('axios').default;


function HomeScreen(){
    const auth = useAuth();
    const navigate = useNavigate();

    React.useEffect(()=>{
        if(auth.getAuthState() === false){
            navigate("../login");
        }
    })

    const serverData = useServerData();
    const [HWSet1Req, setHWSet1Req] = React.useState(0); //HWset1 allocation request
    const [HWSet2Req, setHWSet2Req] = React.useState(0); //HWset2 allocation request
    //const [ProjNameReq,setProjNameReq] = React.useState("Project"+(serverData.getUserData().Projects.length+1).toString()) //Project Name 
    const [ProjNameReq,setProjNameReq] = React.useState("");
    const [ProjIDReq,setProjIDReq] = React.useState("") //Project ID to Join 
    const [modalVal, setModalVal] = React.useState(null);
    const [isDrawerOpened, setDrawerOpened] = React.useState(false);
   

    console.log("USERID: "+auth.getCredentials().UserID);

    React.useEffect(()=>{
        setHWSet1Req(0);
        setHWSet2Req(0);
        setProjNameReq("Project"+(serverData.getUserData().Projects.length+1).toString());
    },[serverData])
    

    /**
     * @brief Event handler for the create Project button
     */
    const createProjectButton = () =>{
        /**@type {serverDataTypes.Project} */
        const newProj = {
            Name: ProjNameReq,
            HWSet1Alloc: HWSet1Req,
            HWSet2Alloc: HWSet2Req
        };

        var user_info = auth.getCredentials()
        serverData.createProject(newProj,user_info.UserID)
    }

    /**
     * @brief Event handler for the join Project button
     */
    const joinProjectButton = () =>{
        /**@type {serverDataTypes.Project} */
        const newProj = {
            ID: ProjIDReq,
            username: auth.getCredentials().UserID
        };
        
        serverData.joinProject(newProj)
    }

    const deleteProjectButton = (projectID) =>{
        /**@type {serverDataTypes.Project} */
        const newProj = {
            ID: projectID,
            username: auth.getCredentials().UserID
        };
        
        serverData.deleteProject(newProj)
    }

    const updateProjectButton = (type, HW1, HW2, ID) =>{
        /**@type {serverDataTypes.Project} */
        const newProj = {
            ID: ID,
            username: auth.getCredentials().UserID,
            action : type,
            HWSet1Alloc: HW1,
            HWSet2Alloc: HW2
        };
        
        serverData.updateProject(newProj)
    }

    const testGetData = () =>{
        /**@type {serverDataTypes.Project} */
        const token = auth.getCredentials().token;
        axios.get('/get_hardware',
        {
            username: auth.getCredentials().UserID
        },{
            headers: {Authorization : `Bearer ${token}`}}
 
        ).then(
            (response)=>{
                //DEBUG          
                console.log(response.data);        
            }
        ).catch((e)=>{
            console.log(e);
        })

    }

    const logoutButton =()=>{
        navigate('/');
        auth.logoutUser();
    }


    const toggleDrawerStatus = () => {
        setDrawerOpened(!isDrawerOpened)
      }
    const closeDrawer = () => {
        setDrawerOpened(false)
      }


    const Hardware = serverData.getGeneralData().map((obj)=>{
        return(
            <div>
            <div className='hardwareName'>
            <p>
                Hardware: {obj.Name}
            </p>
            </div>
            <p>
                Availability: {obj.Availability}
            </p>
            <p>
                Capacity: {obj.Capacity}
            </p>
            </div>
        )
    })

    /**
     * Sub Type that renders a card containing all requisite project information
     * @param {*} props MUST contain a project prop or else this will not work 
     * @returns 
     */
    function ProjCard(props){
        const [hw1q,setHw1q] = useState(0);
        const [hw2q,setHw2q] = useState(0);
        const project = props.project;
        return (   
            <Card variant="outlined">
                {/* {serverData.getPending() && <div>Loading...</div> }  ask about this*/}
                <CardContent>
                <p>
                    Project Name: {`${project.Name}`},
                    Project ID: {`${project.ID}`},
                    HWSet1Allocation: {`${project.HW1}`},
                    HWSet2Allocation: {`${project.HW2}`}
                </p>
                <TextField
                 label=" HW1 Quantity" 
                 value = {hw1q}
                 onChange = {(event)=>{setHw1q(event.target.value)}} />
                <TextField 
                 label=" HW2 Quantity"
                 value = {hw2q}
                 onChange = {(event)=>{setHw2q(event.target.value)}}/>
                <ButtonGroup size="small" orientation="vertical">
                    <Button onClick={() => updateProjectButton("check-in", hw1q, hw2q, project.ID)} >Check in </Button>
                    <Button onClick={() => updateProjectButton("check-out", hw1q, hw2q, project.ID)}>Check out </Button>
                    <Button color='error' onClick={() => deleteProjectButton(project.ID)}>Delete Project</Button>
                </ButtonGroup>
                {/* <div>
                    {auth.getErrorOrNot() && <Alert severity="error"> {auth.getErrorMessage()} </Alert>}
                </div> */}
                </CardContent>
            </Card>
        )
    }



    const ProjectsArr = serverData.getUserData().Projects.map((project)=>{
        return (
            <ProjCard project={project}/>
        )
    })



    //TODO: Format this using Grid so that drawer and list scale properly
    return(
        <div>
            <Box height = "100vh" flex = "1" display = "flex" justifyContent="center" alignItems="center" flexDirection={"column"}>
            <Button onClick={() => toggleDrawerStatus()}>Side Panel
            </Button>

         
            <Drawer variant="temporary"
                    open={isDrawerOpened}
                    onClose={() => closeDrawer()}>
                {/* <Typography >
                   --------------
                   Create Project
                   --------------
                </Typography> */}
                <Box sx={{height: 20}}/>
                <TextField 
                    id="outlined-basic" 
                    label="Hardware Set 1 Allocation" 
                    variant="outlined"
                    value = {HWSet1Req} 
                    onChange={(event)=>{
                        setHWSet1Req(event.target.value);
                    }}
                />
                <Box sx={{height: 20}}/>
                <TextField 
                    id="outlined-basic" 
                    label="Hardware Set 2 Allocation" 
                    variant="outlined" 
                    value = {HWSet2Req}
                    onChange={(event)=>{
                        setHWSet2Req(event.target.value);
                    }}
                />
                <Box sx={{height: 20}}/>
                <TextField 
                    id="outlined-basic" 
                    label="Project Name" 
                    variant="outlined" 
                    value = {ProjNameReq}
                    onChange={(event)=>{
                        setProjNameReq(event.target.value);
                    }}
                />
                
                <Box sx={{height: 20}}/>
                <Button onClick={createProjectButton}>Create Project</Button>

                

                {/* <Button onClick={testGetData}>Test Button</Button> */}
               
                {/* <Button onClick={deleteProjectButton}>Delete Project</Button>
                <Button onClick={() => updateProjectButton("check-in")}>Check in</Button>
                <Button onClick={() => updateProjectButton("check-out")}>Check out</Button> */}
                

                <Box sx={{height: 20}}/>
                {/* <Typography >
                   ----------------
                   Join Project
                   ---------------
                </Typography> */}
                <Box sx={{height: 20}}/>
                <TextField 
                    id="outlined-basic" 
                    label="Project Join ID" 
                    variant="outlined"
                    value = {ProjIDReq}
                    onChange = {(event)=>{setProjIDReq(event.target.value)}} 
                />
                <Box sx={{height: 20}}/>
                <Button onClick={joinProjectButton}>Join Project</Button>

                <div>
                    {auth.getErrorOrNotSideBar() && <Alert severity="error"> {auth.getErrorMessageSideBar()} </Alert>}
                </div> 

                <Box sx={{height: 100}}/>
                {/* <Typography >
                   -------------------
                   General
                   ------------------
                </Typography> */}
                <Button onClick={()=>{navigate('/dataset')}}>Go to Datasets Page</Button>
                <Button onClick={()=>{logoutButton()}}>Logout</Button>  
          

            </Drawer>
            
            <List style={{maxWidth:'100%', maxHeight: '100%', overflow: 'auto'}}>
                <div>
                <p>UserID: {auth.getCredentials().UserID}</p>
                <p>My Total HW Allocation: (HWSet1) {serverData.getUserData().UserTotalAlloc.HW1}, (HWSet2) {serverData.getUserData().UserTotalAlloc.HW2}</p>
                <div>{Hardware}</div>
                <div>
                    {auth.getErrorOrNot() && <Alert severity="error"> {auth.getErrorMessage()} </Alert>}
                </div>
                <div>{ProjectsArr}</div>
                </div>
            </List>
            <Modal //Change from Modals to Dialog in order to allow for textfields within
            open={(modalVal == null ? false : true)}
            onClose={()=>{setModalVal(null)}}
            >
                <Box>
                    <Alert severity={"info"}>{modalVal}</Alert>
                </Box>
            </Modal>
            </Box>

        </div>
    )
}






export {HomeScreen}