import * as React from 'react'
import { Navigate } from 'react-router-dom';
import {useAuth} from './authentication'

const lodash = require('lodash');
const config = require('./config');
const axios = require('axios').default;

/**
 * @brief This context will only ever hold our Server Data Object
 * @type {React.Context<ServerDataObject>}
 */
const ServerDataContext = React.createContext()


/**
 * @returns {ServerDataObject} React hook allowing components under ServerData Provider to utilize User Data methods
 */
function useServerData(){
    return React.useContext(ServerDataContext);
}



/**
 * @brief This function is a context provider for the ServerData Context. 
 * It contains methods to update data related to the server, which are contained within the context value.
 */
function ServerDataProvider({children}){
    //TODO: userData, generalData states currently contain debug default states. These need to be removed.
    //NOTE: userData is a UserDataType Object (check the typedefs) I just didn't know how to comment that when its an inline declaration
    const auth = useAuth();
    const [isPending, setPending] = React.useState(true)
    const [userData, setUserData] = React.useState(
        {
            "UserID":auth.getCredentials().UserID,  
            "UserTotalAlloc":{
                "HW1":0,
                "HWSet2":0
            },
            "Projects":[
                // {
                //     "Name": "Project1",
                //     "HWSet1Alloc": 20,
                //     "HWSet2Alloc":30
                // },
                // {
                //     "ID": "Project2",
                //     "HWSet1Alloc": 40,
                //     "HWSet2Alloc": 50
                // }   
            ], 
        }   
    );
    const[generalData, setGeneralData] = React.useState([]);


    /**
     * @brief Get's the user's data from our internal state
     * @returns {UserDataObject} Object representing all of the user's data
     */
    const getUserData = ()=>{
        return userData
    }
    const getPending = () =>{
        return isPending
    }

    /**
     * @brief This function refreshes the stored user data from the server. Should be called at the end of each update we send out. Also should be called on load.
     * @returns 
     */
    const fetchUserData = () =>{
        console.log("Fetch User Data")
        const token = auth.getCredentials().token;
        axios.post('/projects_homepage',
            {
                token: token
            },{
                headers: {Authorization : `Bearer ${token}`}}
        ).then((response)=>{
            setUserData(response.data);
            setPending(false)
            auth.resetError()
        }).catch((error)=>{
            console.error(error);
        })
        return;
    }

    /**
     * @brief This function returns the stored general data regarding the server. 
     * @returns {GeneralDataObject}
     */
    const getGeneralData =()=>{
        return generalData
    }


    const joinProject = (data) => {

        return new Promise((resolve,reject)=>{
            const token = auth.getCredentials().token;
            axios.post('/join_project',
            {
                username: data.username,
                projectID: data.ID,
            },{
                headers: {Authorization : `Bearer ${token}`}}
            ).then(
                (response)=>{
                    console.log(response.data["Response"]); //DEBUG
                    if(response.data["Response"]===true){
                        console.log(response.data);
                        fetchUserData();
                        resolve();
                        auth.resetErrorSideBar();
                    
                    } else {
                        console.log(response.data);
                        reject(response.data["Message"])
                       auth.setErrorMessageSideBarHandler(response.data["Message"])
                    }
                }
            ).catch((e)=>{
                console.log(e);
            })
        })
    }

    const createProject = (data, username) =>{

        return new Promise((resolve,reject)=>{
            const token = auth.getCredentials().token;
            axios.post('/create_project',
            {
                username: username,
                Name:data.Name,
                HWSet1Alloc:data.HWSet1Alloc,
                HWSet2Alloc: data.HWSet2Alloc
                
            },{
                headers: {Authorization : `Bearer ${token}`}}
            ).then(
                (response)=>{
                    console.log(response.data["Response"]); //DEBUG
                    if(response.data["Response"]===true){
                        console.log(response.data);
                        fetchUserData();
                        fetchGeneralData();
                        resolve();
                        auth.resetErrorSideBar();
                      
                    } else {
                        console.log(response.data);
                        reject(response.data["Message"])
                        auth.setErrorMessageSideBarHandler(response.data["Message"])
                    }   
                }
            ).catch((e)=>{
                console.log(e);
            })
        })

        
    }

    const deleteProject = (data) => {

        return new Promise((resolve,reject)=>{
            const token = auth.getCredentials().token;
            axios.post('/delete_project',
            {
                username: data.username,
                projectID: data.ID,
            },{
                headers: {Authorization : `Bearer ${token}`}}
            ).then(
                (response)=>{
                    console.log(response.data["Response"]); //DEBUG
                    if(response.data["Response"]===true){
                        console.log(response.data);
                        fetchUserData();
                        fetchGeneralData();
                        resolve();
                        auth.resetError()
                    } else {
                        console.log(response.data);
                        reject(response.data["Message"])
                        auth.setErrorMessageHandler(response.data["Message"])
                    }
                }
            ).catch((e)=>{
                console.log(e);
            })
        })
    }
    const updateProject = (data) => {

        return new Promise((resolve,reject)=>{
            const token = auth.getCredentials().token;
            axios.post('/update_project',
            {
                username: data.username,
                projectID: data.ID,
                action: data.action,
                HWSet1Alloc:data.HWSet1Alloc,
                HWSet2Alloc: data.HWSet2Alloc
            },{
                headers: {Authorization : `Bearer ${token}`}}
            ).then(
                (response)=>{
                    console.log(response.data["Response"]); //DEBUG
                    if(response.data["Response"]===true){
                        console.log(response.data);
                        fetchUserData();
                        fetchGeneralData();
                        resolve();
                        auth.resetError()
                    } else {
                        console.log(response.data);
                        //setErrorMessage
                        reject(response.data["Message"])
                        auth.setErrorMessageHandler(response.data["Message"])
                    }
                }
            ).catch((e)=>{
                console.log(e);
            })
        })
    }


    /**
     * @brief This function returns the stored general data regarding the server. Should be called at the end of each update we send out. Also should be called on load.tashed changes
     */
    const fetchGeneralData = () =>{
        const token = auth.getCredentials().token;
        axios.get('/get_hardware',
        ).then((response)=>{
            console.log(response.data)
            setGeneralData(response.data["Hardware"]);
        }).catch((error)=>{
            console.log(error)
        })
        return;
    }





    /**
     * @brief 
     * @returns {Promise<boolean>}
     */
    const logout =()=>{
        return new Promise((resolve,reject)=>{
            const token = auth.getCredentials().token;
            console.log(token)
            axios.post('/logout',
                {
                    token: token
                }
                ,{
                    headers: {Authorization : `Bearer ${token}`}}
            ).then((response)=>{
                console.log(response)
            }).catch((error)=>{
                console.log(error)
               
            })
        })
    }



    React.useEffect(()=>{ //run fetches on load one time
        fetchUserData();
        fetchGeneralData();
    },[])

    
    return(
        <ServerDataContext.Provider value = {
            {
                getUserData,
                getGeneralData,
                updateProject,            
                fetchUserData,
                logout,
                fetchGeneralData,
                createProject,
                joinProject,
                deleteProject,
                getPending

            }
        }>
            {children}
        </ServerDataContext.Provider>
    )

}

export {ServerDataProvider, useServerData}


