/** Type def for the ServerDataClass
 * @typedef {Object} ServerDataObject 
 * @property {getUserDataFuncType} getUserData Get's the user's data from our internal state
 * @property {getGeneralDataFuncType} getGeneralData This function returns the stored general data regarding the server
 * @property {Function} fetchUserData This function refreshes our internal cache of the User Data
 * @property {Function} fetchGeneralData This function refreshes our internal cache of the General Server Data
 * @property {logoutFunctionType} logout This allows the user to logout
 * @property {createProjectFuncType} createProject Creates a project on the server
 * **IMPORTANT:** Any data passed to this must not be an updated copy of the object returned by getUserData. You must either do a deep clone of that object, or create
 * an entirely new object to set.
 
 */

