import { Box } from '@mui/system';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import {useAuth} from '../contexts/authentication'
import { useNavigate } from "react-router-dom";
import { useState } from "react";
import { Modal } from "@mui/material";
import Alert from '@mui/material/Alert';


/**
 * @brief This component will render our initial login screen. 
 * It will authenticate an input userId/password sequence against the database and if it does not exist, it will error
 * TODO: Create a theme for the root box to make the login screen look nicers
 */
function LoginScreen(){
    const auth = useAuth();
    const navigate = useNavigate();

    const [modalVal, setModalVal] = useState(null);
    const [pass,setPass] = useState(null);
    var [user,setUser] = useState(null);
    var validEmailRegex = new RegExp('^[a-zA-Z0-9._:$!%-]+@[a-zA-Z0-9.-]+.[a-zA-Z]$'); // https://www.tutorialspoint.com/regex-in-reactjs, Regex to validate email id
    const isValidEmail = ()=>{return validEmailRegex.test(user)}

    return(
        <Box height = "100vh" flex = "1" display = "flex" justifyContent="center" alignItems="center" flexDirection={"column"}>
            <TextField id="outlined-basic" label="Enter email" variant="outlined" sx={{columnGap:10}}
                onChange={(event)=>{
                    setUser(event.target.value);
                }}
            />
            <Box
                sx={{height: 20}}
            />
            <TextField type={"password"} id="outlined-basic" label="Password" variant="outlined" 
                onChange={(event)=>{
                    setPass(event.target.value);
                }}
            />
            <Box
                sx={{height: 20}}
            />
            <Button variant="contained"
                onClick = {(()=>{
                    if(isValidEmail() && !(pass===null)){
                        auth.authenticateUser(user,pass).then(
                            (success)=>{
                                navigate("../home");  
                            },
                            (err)=>{
                                setModalVal(err)
                            }
                        )
                        setModalVal("Loading...")

                    } else if (pass===null){
                        setModalVal("Empty Password.")
                    } else {
                        setModalVal("Invalid Email address.")
                    }
                })}
            >
                Sign in 
            </Button>
            <Button onClick={()=>{navigate('/')}}>Back</Button>
            <Modal
            open={(modalVal == null ? false : true)}
            onClose={()=>{setModalVal(null)}}
            >
                <Box>
                    <Alert severity={(modalVal==="Loading..." ? "info":"error")}>{modalVal}</Alert>
                </Box>
            </Modal>
        </Box>
    )
}

export default LoginScreen;