import { useNavigate } from "react-router-dom";
import { Box } from "@mui/material";
import { Button } from "@mui/material";
import { Typography } from "@mui/material";
import { useServerData } from "../contexts/serverData";
import React from "react";
import { useAuth } from "../contexts/authentication";
import { Card, CardContent, List, Link } from '@mui/material';
const axios = require('axios').default;

function DatasetScreen(){
    const navigate = useNavigate();
    const auth = useAuth();
    const [datasets,setDatasets] = React.useState([ //TODO: Should eventually be moved into the serverData context
      (
        <Card>
          <CardContent>
            <p>Loading Datasets</p>
          </CardContent>         
        </Card>
      )
    ]); //TODO: Should contain a loading card
    console.log(datasets);
    React.useEffect(()=>{
      if(auth.getAuthState() === false){
          navigate("../login");
      }
    })

    
    //TODO: Once we figure out nested routes and nest this under the home route, we should migrate this fetch logic to the serverData context to better encapsulate everything
    //This will also allow for pre-loading and caching.
    React.useEffect(()=>{ //Load datasets on first load
      console.log("Effect Fire")
      const respProm = axios.get('/get_datasets');
      respProm.then((response)=>{
        /**@type {Array<DatasetObj} */
        const dataArr = response.data.datasets;
        const elemArr = dataArr.map((dataset)=>{
          return (
            <Card>
              <CardContent>
                <p>Name: {dataset.record_name}</p>  
                <p>Comments: </p>
                {dataset.comments.map((comment)=>{
                  return(
                    <p>{comment}</p>
                  )
                })}
                <p>Number of Signals: {dataset.n_sig}</p>
                <p>Sampling Frequency: {dataset.fs}</p>
                <a href={`${dataset.link}`}>Download</a>
              </CardContent>         
            </Card>
          )
        })
        setDatasets(elemArr);
      }).catch((error)=>{
        console.log(error)
      })
    },[])

  const logoutFunction = () =>{
    navigate('/')
    auth.logoutUser()
  }
  
    return(
      <Box height = "100vh" flex = "1" display = "flex" justifyContent="center" alignItems="center" flexDirection={'column'}>
           <Typography >
                Download Datasets from the MIT-BIH Arrythmia database here
            </Typography>
            <List style={{maxWidth:'100%', maxHeight: '100%', overflow: 'auto'}}>
               {datasets}
            </List>
            <Button onClick={()=>{navigate('../home')}}>Go to Home Page</Button>
            <Button onClick={()=>{logoutFunction()}}>Logout</Button>
      </Box> 
    )
}

export {DatasetScreen}


/**
 * Type def for Dataset obj
 * @typedef DatasetObj
 * @property {Array<String>} comments General comments about data
 * @property {Number} fs sampling frequency of the record
 * @property {string }link link to download the dataset
 * @property {number} n_sig number of signals
 * @property {string} record_name dataset name
 * @
 */