import * as React from "react";
import { useServerData } from './serverData';

const axios = require('axios').default;
const config = require('./config');

//Create an authentication context to hold auth global object
//Docs: https://reactjs.org/docs/context.html#consuming-multiple-contexts
//NOTE: I didn't put the initialization of this here (instead did it by creating the auth obj in AuthProvider and setting it)
// because putting that big of an inline object will be messy
const authContext = React.createContext(); 


/**
 * @brief This react hook functions exactly as the `useContext` hook does, but specifically for our authentication context.
 * What that means is that every time the global authentication context's value changes, any child component that has used this hook will re-render and recieve a fresh copy. 
 * This lets us access the authentication context from all children of an AuthProvider 
 * @returns {AuthObject} authObject
 */
function useAuth(){
    return React.useContext(authContext);
}


/**
 *  @brief This react component wraps all react elements that may need access to the authentication context. 
 * This **should only ever be used once** unless you have multiple authentications within the app.
 */
function AuthProvider({children}){
    const [UserID,setUserID] = React.useState(null);//store the user's userID
    const [Password, setPassword] = React.useState(null); //store the user's password 
    const initializeStateToken = () => JSON.parse(localStorage.getItem("AUTH_TOKEN"));
    const [authToken, setAuthToken] = React.useState(initializeStateToken);

    const initializeState = () => JSON.parse(localStorage.getItem("AUTH_STATE"));
    const [isAuthenticated, setAuthState] = React.useState(initializeState); //store auth
    const serverData = useServerData();

    const [errorMessage, setErrorMessage] = React.useState(null);
    const [errorOrNot, setErrorOrNot] = React.useState(false);
    const [errorMessageSideBar, setErrorMessageSideBar] = React.useState(null);
    const [errorOrNotSideBar, setErrorOrNotSideBar] = React.useState(false);

   const getErrorOrNot =()=>{
       return errorOrNot
   }

   const getErrorMessage =()=>{
       return errorMessage
   }

   const setErrorMessageHandler = (message) =>{
       setErrorMessage(message)
       setErrorOrNot(true)
   }

    const resetError = () => {
        setErrorOrNot(false);
        setErrorMessage(null)
    };

    const getErrorOrNotSideBar =()=>{
       return errorOrNotSideBar
   }

   const getErrorMessageSideBar =()=>{
       return errorMessageSideBar
   }

   const setErrorMessageSideBarHandler = (message) =>{
       setErrorMessageSideBar(message)
       setErrorOrNotSideBar(true)
   }

   const resetErrorSideBar = () => {
        setErrorOrNotSideBar(false);
        setErrorMessageSideBar(null)
    };

     React.useEffect(()=>{
        const data = window.localStorage.getItem("AUTH_STATE");
        const dataToken = window.localStorage.getItem("AUTH_TOKEN");
        console.log("local store tester" + isAuthenticated);
        console.log("local store tester for token" + authToken);
        setAuthState(JSON.parse(data));
        setAuthToken(JSON.parse(dataToken));
    },[])

     React.useEffect(()=>{
        
        window.localStorage.setItem('AUTH_TOKEN', JSON.stringify(authToken))
        window.localStorage.setItem('AUTH_STATE', JSON.stringify(isAuthenticated))
        console.log("test" + isAuthenticated)
    }, [isAuthenticated, authToken])
       
    /**
     * @async
     * @param {string} username username to send to the server
     * @param {string} password password to send to the server
     * @returns {Promise<boolean>} true/false depending on authentication
     */
    const authenticateUser = async(username,password)=>{
        return new Promise((resolve,reject)=>{
            axios.post('/authenticate',
            {
                username:username,
                password:password
            }
            ).then(
                (response)=>{
                    if(response.data["Response"]===true){ //TODO: Add auth token setting here
                        console.log(true);
                        setUserID(username);
                        setPassword(password);
                        setAuthToken(response.data["token"]);
                        setAuthState(true);
                        resolve();
                    } else {
                        console.log(false);
                        reject(response.data["Message"])
                    }
                }
            ).catch((e)=>{
                console.log(e);
            })
        })
    }

     /**
     * @brief 
     * @returns {Promise<boolean>}
     */
    const logoutUser =()=>{
        return new Promise((resolve,reject)=>{
            const token = getCredentials().token;
            console.log(token)
            axios.post('/logout',
                {
                    token: token
                }
                ,{
                    headers: {Authorization : `Bearer ${token}`}}
            ).then((response)=>{
                setAuthToken(null);
                setAuthState(false);
                console.log(response)
            }).catch((error)=>{
                console.log(error)
            })
        })
    }

    

    // Need to post to backend

    const createUser = async(username,password)=>{
        // return new Promise((resolve,reject)=>{
        //     setTimeout(()=>{
        //         setUserID(username);
        //         setPassword(password);
        //         setAuthState(true);
        //         resolve();
        //     },2000)
        // })

        return new Promise((resolve,reject)=>{

            
            axios.post('/register',
            {
                username:username,
                password:password
            }
            ).then(
                (response)=>{

                    console.log(response.data["Response"]); //DEBUG
                    if(response.data["Response"]===true){
                        console.log(true);
                        setUserID(username);
                        setPassword(password);
                        setAuthState(true);
                        setAuthToken(response.data["token"]);

                        resolve();
                    } else {
                        console.log(false);
                        reject(response.data["Message"])
                    }
                }
            ).catch((e)=>{
                console.log(e);
            })
        })
       
    }

    const getCredentials = ()=>{
        return {
            "UserID":UserID,
            "Password":Password,
            "token":authToken
        }
    }


    const getAuthState = () => {
        console.log(isAuthenticated)
        return isAuthenticated;
    }

    const contextValue = {
        authenticateUser,
        createUser,
        getCredentials,
        getAuthState,
        logoutUser,
        getErrorMessage,
        getErrorOrNot,
        setErrorMessageHandler,
        resetError,
        getErrorOrNotSideBar,
        getErrorMessageSideBar,
        setErrorMessageSideBarHandler,
        resetErrorSideBar
    };

    return(
        <authContext.Provider value = {contextValue}> {/* Create an authentication provider object with the authContext set to the auth object. This allows all children to access the auth object*/}
            {children}
        </authContext.Provider>
    );

}

export {useAuth, AuthProvider};


