import { Box } from "@mui/system";
import { TextField } from "@mui/material";
import { Button } from "@mui/material";
import {useAuth} from '../contexts/authentication'
import { useNavigate } from "react-router-dom";
import React, { useState } from "react";
import { Modal } from "@mui/material";
import Alert from '@mui/material/Alert';



/**
 * @brief This screen is the sign up screen where users will be able to create a new user and then sign in. 
 * It will take a userID and password, check against the database if the user already exists, and if not, it creates a user and takes them to the home page
 * TODO: Add back-end communication to check if account already exists and link to on-click
 */
function SignupScreen(){
    const auth = useAuth();
    const navigate = useNavigate();

    const [modalVal, setModalVal] = useState(null);
    const [user,setUser] = useState(null);
    const [pass1,setPass1] = useState(null);
    const [pass2,setPass2] = useState(null);
    const passMatch = ()=>{
        var cond1 = (pass1 === pass2 ? true:false)
        var cond2 = (pass1 === null ? false:true)
        return cond1 && cond2
    }

    var validEmailRegex = new RegExp('^[a-zA-Z0-9._:$!%-]+@[a-zA-Z0-9.-]+.[a-zA-Z]$'); // https://www.tutorialspoint.com/regex-in-reactjs, Regex to validate email id
    const isValidEmail = ()=> {return validEmailRegex.test(user)}
    return(
        <Box height = "100vh" flex = "1" display = "flex" justifyContent="center" alignItems="center" flexDirection={"column"}>
            <TextField id="outlined-basic" label="Enter your email" variant="outlined" sx={{columnGap:10}}
                onChange={(event)=>{
                    setUser(event.target.value);
                }}
            />
            <Box sx={{height: 20}}/>
            <TextField type={"password"} id="outlined-basic" label="Enter password" variant="outlined" 
                onChange={(event)=>{
                    setPass1(event.target.value);
                }}
            />
            <Box sx={{height: 20}}/>
            <TextField type={"password"} id="outlined-basic" label="Confirm password" variant="outlined" 
                onChange={(event)=>{
                    setPass2(event.target.value);
                }}
            />
            <Box sx={{height: 20}}/>
            <Button variant="contained"
                onClick = {(()=>{
                    if(passMatch() && isValidEmail()){
                        auth.createUser(user,pass1).then(
                            (success)=>{
                                navigate("../home")
                            },
                            (err)=>{
                                setModalVal(err)
                            }
                        )
                        setModalVal("Loading...")
                    } else if (!passMatch() && !isValidEmail()){
                        setModalVal("Invalid Username and Password.");
                    } else if (!passMatch()) {
                        setModalVal("Invalid Password.");
                    } else if (!isValidEmail()){
                        setModalVal("Invalid Username, ensure it is a valid email address")
                    }
                })}
            >
                Sign up
            </Button>
            <Button onClick={()=>{navigate('/')}}>Back</Button>
            <Modal
            open={(modalVal == null ? false : true)}
            onClose={()=>{setModalVal(null)}}
            >
                <Box>
                    <Alert severity={(modalVal==="Loading..." ? "info":"error")}>{modalVal}</Alert>
                </Box>
            </Modal>
        </Box>
    )
}



export default SignupScreen;
