import '../css/App.css';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import { Box } from '@mui/system';
import {useNavigate} from 'react-router-dom'

import LoginScreen from './login'
import {HomeScreen} from './home'
import SignupScreen from './signup';
import {AuthProvider} from '../contexts/authentication'
import {ServerDataProvider} from '../contexts/serverData'
import { DatasetScreen } from './dataset';



/**
 * @brief This component renders the initial screen that users will see and provide them the option to sign in or create an account
 * TODO: This needs to be made more graphically pleasing
 */
function RootScreen(){
  const navigate = useNavigate();
  return(
    <Box height = "100vh" flex = "1" display = "flex" justifyContent="center" alignItems="center ">
        <ButtonGroup variant="contained" aria-label="outlined button group" orientation='vertical' size = 'large' >
        <Button onClick = {()=>{
          navigate("login")
        }}>
           Login with existing account 
        </Button>
        <Button onClick = {()=>{
          navigate("signup")
        }}>
          Sign up for new account
        </Button>
        </ButtonGroup>
    </Box> 
  )
}

function App() {
  return (
    <AuthProvider>
    
    <BrowserRouter>
      <Routes>
        <Route path = "/"/>
          <Route index element = {<RootScreen/>}/>
          <Route path = "home" element={
            <ServerDataProvider>
              <HomeScreen/>
            </ServerDataProvider>
          }/>
          <Route path = "dataset" element={<DatasetScreen/>}/> {/**TODO: Figure out how to nest this inside the home route, so that it can be gated within the ServerDataProvider */}
          <Route path= 'login' element ={<LoginScreen/>}/>  
          <Route path = 'signup' element = {<SignupScreen/>}/>
      </Routes>
    </BrowserRouter>
    
    </AuthProvider>
  );
}




export default App;